import * as React from "react"
import { Grid } from "@mui/material"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { ArtworkInfo } from "./ArtworkInfo"
import { ArtworkBannerStyles } from "./ArtworkBannerStyles"

export const ArtworkBanner = ({ artwork }) => {
    const { title, picture } = artwork

    const image = getImage(artwork.gatsbyImage)

    return (
        <>
            <ArtworkBannerStyles>
                <GatsbyImage
                    className="banner__image"
                    imgClassName="banner__image--content"
                    image={image}
                    alt={title + " - Banner"}
                />

                <div className="container">
                    <div className="banner__content">
                        <Grid container spacing={8}>
                            <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                                <ArtworkInfo artwork={artwork} />
                            </Grid>
                            <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                                <a href={picture.to_original_path()} target="_BLANK" rel="noreferrer">
                                    <img
                                        src={picture.to_framed_path()}
                                        alt={title}
                                        height={picture.get_framed_height()}
                                    />
                                </a>
                            </Grid>
                        </Grid>
                    </div>
                </div>
                <div className="gradient"></div>
            </ArtworkBannerStyles>
            <span id="topContent"></span>
        </>
    )
}
