import React from "react"
import Seo from "../components/SEO"
import { Artist, ArtWork } from "../models/artist"
import Layout from "../components/Layout"
import { ArtworkBanner } from "../components/Artwork/ArtworkBanner"
import Features from "../components/Features/Features"
import { ArtworkList } from "../components/ArtworkList"

const ProductTemplate = ({ pageContext: { artist: cfArtist, artwork: cfArtwork } }) => {
    const artist = Artist.registerFromContentful(cfArtist)
    const artWork = ArtWork.fromContentful(cfArtwork, cfArtist)
    return (
        <>
            <Seo title={artWork.title} />
            <Layout>
                <ArtworkBanner artwork={artWork} />
                <Features
                    artworks={artist.featuredArtwork}
                    title={"Featured Works from " + artist.name + "."}
                    introduction="Vivamus quam mauris, pulvinar vel mauris id, interdum semper neque. Proin malesuada libero eget tellus scelerisque, id egestas tortor egestas."
                />
                <ArtworkList artworks={artist.artwork} />
            </Layout>
        </>
    )
}

export default ProductTemplate
